const baseFont = parseInt(document.documentElement.style.fontSize)

export default {
    window: {
        width: document.body.clientWidth,
        height: document.body.clientHeight,
        //screenHeight: Dimensions.get('window').height
    },
    theme: {
        mainColor: '#ff4f4f',
        minorColor: '#333333',
        lineColor: '#eeeeee'
    },
    px2rem(pxv) {
        return `${parseInt(pxv) / 75}rem` 
    },
    rem2px(remv) {
        return `${parseFloat(remv) * 75}px`
    },
    getDpr() {
        let dpr = window.devicePixelRatio;
        const isIPhone = window.navigator.appVersion.match(/iphone/gi);
        if(dpr > 3) dpr = 3;
        if(!isIPhone) dpr = 1;
        return dpr;
    },
    getFontSize(fspx) {
        const dpr = this.getDpr();
        return `${fspx * dpr}px`;
    },
    style: {
        flexbox() {
            return {
                display: 'flex',
                display: '-webkit-flex'
            }
        }   
    },
    icon64: {
        back:'data:image/png;base64,',
        right:'data:image/png;base64,',
        backWhite:'data:image/png;base64,',
        arrowDown:'data:image/png;base64,',
        arrowUp:'data:image/png;base64,'
    }
}



